<template>
  <div class="movies-container" :data-rowcount="rowCount" :data-count="movies.length">
    <div v-if="!!title" class="mod-head">
      <div class="mod-title text-ellipsis">
        {{ title }}
        <!-- <span class="subtitle hide-sm">
          {{ subtitle }}
        </span> -->
      </div>
      <div class="mod-page">
        <button @click="prev" :class="['btn_prev', currentPage === 1 && 'disabled']" title="上一页">
          <svg class="svg_icon svg_icon_prev" viewBox="0 0 6 10" width="6" height="10">
            <path
              d="M1.4 4.7L5 1M1.3 5.3L5 9"
              fill="none"
              stroke="currentColor"
              stroke-width="1.4"
              stroke-linecap="round"
            ></path>
          </svg>
        </button>
        <span class="page_num" data-count="3" data-page="2">{{ currentPage + '/' + totalPage }}</span>
        <button @click="next" :class="['btn_next', currentPage === totalPage && 'disabled']" title="下一页">
          <svg class="svg_icon svg_icon_next" viewBox="0 0 6 10" width="6" height="10">
            <path
              d="M4.6 4.7L1 1M4.7 5.3L1 9"
              fill="none"
              stroke="currentColor"
              stroke-width="1.4"
              stroke-linecap="round"
            ></path>
          </svg>
        </button>
      </div>
    </div>
    <div class="flex-items">
      <router-link
        class="item-wrap"
        v-for="(movie, index) in movies"
        :key="movie.id"
        :to="`/movie/${movie.id}`"
        v-show="index >= minIndex && index < maxIndex"
      >
        <MovieItem :movie="movie" />
      </router-link>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent } from 'vue';
import { throttle } from 'lodash';
import MovieItem from './MovieItem.vue';

export default defineComponent({
  components: {
    MovieItem
  },
  props: {
    title: {
      type: String
    },
    subtitle: {
      type: String
    },
    movies: {
      type: Array
    }
  },
  mounted() {
    window.addEventListener('resize', throttle(this.getMoviesRowCount, 200));
    this.getMoviesRowCount();
  },
  methods: {
    getMoviesRowCount() {
      const appDocument = document.getElementById('app');
      var appStyles = window.getComputedStyle(appDocument);
      this.rowCount = appStyles.getPropertyValue('--movie-grid-items-per-row');
    },
    prev() {
      this.currentPage--;
    },
    next() {
      this.currentPage++;
    }
  },
  computed: {
    totalPage() {
      return Math.ceil(this.movies.length / this.rowCount);
    },
    maxIndex() {
      return this.rowCount * this.currentPage;
    },
    minIndex() {
      const remainder = this.movies.length % this.rowCount;
      if (this.totalPage === this.currentPage && remainder > 0) {
        return this.rowCount * (this.currentPage - 2) + remainder;
      }
      return this.rowCount * (this.currentPage - 1);
    }
  },
  data() {
    return {
      rowCount: 3,
      currentPage: 1
    };
  }
});
</script>

